<template>
<div>
  <div>{{count}}</div>
  <div>{{count4}}</div>
  <div>{{count3}}</div>
  <button @click="add">点击+请</button>
  <button @click="jian($store.state,{count:count2}
   )">点击-请</button>
  <button @click="typ">狠狠点</button>
  <div>{{type}}</div>
</div>
</template>

<script>
  import { mapState,mapMutations,mapActions} from 'vuex'
  export default {
    data(){
      return {
        count2:100
      }
    },
    methods:{
      ...mapMutations([
        'add',
        'jian'
        ]),
      // ...mapActions([
      //   type:'typ'
      // ])
      typ(){
        this.$store.dispatch("typ","shit");
      }
    },
    computed:{
      ...mapState({
        count: 'count',
        count4:'count4',
        count3 (state){
          return this.count2 + state.count
        },
        type:'type'
      })
    }
  }
</script>

<style>
  
</style>


